import "./App.css";
import Container from "react-bootstrap/Container";
import Header from "./components/header";
import GeoTag from "./Project/Clusters";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

function App() {
  return (
    <div className="App">
      <Container fluid>
        <Router>
          <Switch>
            <Route path="/" exact>
              <Header />
            </Route>
            <Route path="/GeoTag">
              <GeoTag />
            </Route>
          </Switch>
        </Router>
      </Container>
    </div>
  );
}

export default App;
