import { GoLocation } from "react-icons/go";
import React, { useState, useEffect } from "react";

import "../../components/main.css";
function GeoTag() {
  const [city, setCity] = useState("");
  const [dist, setDistance] = useState(50);
  const [cities, setCities] = useState([]);
  const [nearCity, setNearCity] = useState([]);

  const [Latitude, setLat] = useState(0.0);
  const [Longitude, setLong] = useState(0.0);

  useEffect(() => {
    //-----
    fetch("./indCities.json")
      .then((response) => response.json())
      .then((data) => {
        setCities(data.india);
      });
    console.log("dd");
  }, []);

  const GetNearestCity = (c) => {
    let cCity = c || city;
    if (cCity.trim() == "") {
      setNearCity([]);
      return;
    }
    let cName = cCity.toLowerCase();
    let matchCity = cities.filter((e) => e.city.toLowerCase() == cName);
    if (matchCity.length == 0) {
      setNearCity([]);
      return;
    }
    let lat = matchCity[0].lat;
    let long = matchCity[0].long;
    let nCity = [];
    cities.forEach((e) => {
      let d = Math.sqrt(
        Math.pow(69.1 * (e.lat - lat), 2) +
          Math.pow(69.1 * (long - e.long) * Math.cos(e.lat / 57.3), 2)
      );
      if (d <= dist) {
        nCity.push({ city: e.city, d: d });
      }
      //return e.city.indexOf(city) != -1 && dist < 0;
    });
    setNearCity(nCity);
  };
  return (
    <div className="geoBox">
      <div className="geoForm">
        <div>
          <label>Enter City Name : </label>
          <input
            type="text"
            value={city}
            onChange={(e) => {
              setCity(e.target.value);
              GetNearestCity(e.target.value);
            }}
            id="cityName"
          />
        </div>
        <h6>-OR-</h6>
        <div>
          <label>Enter Lat : </label>
          <input
            type="text"
            value={Latitude}
            onChange={(e) => {
              setLat(e.target.value);
              GetNearestCity(e.target.value);
            }}
            id="latVal"
            disabled
          />
          <br />
          <label>Enter Lon : </label>
          <input
            type="text"
            value={Longitude}
            onChange={(e) => {
              setLong(e.target.value);
              GetNearestCity(e.target.value);
            }}
            id="longVal"
            disabled
          />
        </div>
        <div>
          <label for="distance">Range</label>
          <input
            type="range"
            id="distRange"
            name="distance"
            min="25"
            max="1000"
            value={dist}
            onChange={(e) => {
              setDistance(e.target.value);
              GetNearestCity();
            }}
            step="10"
          />
          <label>{`${dist} Kms`}</label>
        </div>
      </div>
      <div className="result">
        {nearCity.length == 0 ? (
          <h5>
            No places near '{city}' at '{dist} kms'
          </h5>
        ) : (
          nearCity.map((e) => {
            return (
              <div>
                <GoLocation className="loc-logo" />
                <h6>{e.city}</h6>
                <p>distance : {Math.round(e.d) + " kms"}</p>
              </div>
            );
          })
        )}
      </div>
    </div>
  );
}

export default GeoTag;
